.employeeList__list {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, 250px);
}

.employeeList__employee {
  padding: 0.5rem;
  display: grid;
  grid-template-columns: auto 150px;

  background: white;
  -webkit-box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.3);
          box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.3);
}



