legend {
  font-weight: bold;
}

input[type='text'],
input[type='password'],
input[type='email'] {
  display: block;
  padding: 5px;
  margin-bottom: 0.5rem;
  border: 1px solid lightgrey;
  border-style: solid;
  border-radius: 3px;
  color: #333;
}



